import { ComponentProps, PropsWithChildren } from "react";

export interface IMessageBoxProps
  extends PropsWithChildren<any>,
    ComponentProps<any> {
  title?: string;
  type: "Confirmation" | "Error" | "Success" | "Warning" | "Information";
  onCloseClick?(): void;
}

export const MessageBox = ({
  title,
  type,
  children,
  onCloseClick,
}: IMessageBoxProps) => {
  return (
    <div
      className={`fd-message-box-docs-static fd-message-box fd-message-box--${type.toLowerCase()} fd-message-box--active`}
    >
      <section className="fd-message-box__content fd-message-box__content--m">
        <header className="fd-bar fd-bar--cozy fd-bar--header fd-message-box__header">
          <div className="fd-bar__left">
            <div className="fd-bar__element">
              <h2 className="fd-title fd-title--h5">{title || type}</h2>
            </div>
          </div>
        </header>
        <div className="fd-message-box__body">{children}</div>
        <footer className="fd-bar fd-bar--cozy fd-bar--footer fd-message-box__footer">
          <div className="fd-bar__right">
            <div className="fd-bar__element">
              <button
                className="fd-button fd-button--emphasized fd-message-box__decisive-button"
                onClick={() => onCloseClick?.()}
              >
                Close
              </button>
            </div>
          </div>
        </footer>
      </section>
    </div>
  );
};
export default MessageBox;
