import { ComponentProps, FC, PropsWithChildren, SyntheticEvent } from "react";

export interface IBasicPage
  extends PropsWithChildren<any>,
    ComponentProps<any> {
  headerTitle?: string;
  showGoBack?: boolean;
  onGoBack?(event?: SyntheticEvent): void;
  headerAction?: {
    icon?: string;
    label?: string;
    onClick?(event?: SyntheticEvent): void;
  };
}

/**
 *
 * @param {IBasicPage} param0
 * @returns
 */
export const BasicPage: FC<IBasicPage> = ({
  headerTitle,
  showGoBack,
  onGoBack,
  headerAction,
  children,
  className,
}: IBasicPage) => {
  return (
    <main className={`fd-page ${className || ""}`}>
      <header>
        <div className="fd-bar fd-bar--page fd-bar--header">
          <div className="fd-bar__left">
            <div className="fd-bar__element">
              {showGoBack && (
                <button
                  className="fd-button fd-button--transparent"
                  aria-label="Go back"
                  onClick={onGoBack}
                >
                  <i className="sap-icon--navigation-left-arrow"></i>
                </button>
              )}
            </div>
            {headerTitle && (
              <div className="fd-bar__element">{headerTitle}</div>
            )}
          </div>
          <div className="fd-bar__right">
            {headerAction && (
              <div className="fd-bar__element">
                <button
                  className="fd-button fd-button--transparent"
                  title={headerAction.label}
                  aria-label={headerAction.label}
                  onClick={headerAction.onClick}
                >
                  <i className={`sap-icon--${headerAction.icon}`}></i>
                </button>
              </div>
            )}
          </div>
        </div>
      </header>
      <div className="fd-page__content" role="region">
        {children}
      </div>
      <footer>
        <div className="fd-bar fd-bar--page fd-bar--footer">
          <div className="fd-bar__right">
            {/* <div className='fd-bar__element'>
              <button className='fd-button fd-button--emphasized fd-button--compact'>
                Save
              </button>
            </div>
            <div className='fd-bar__element'>
              <button className='fd-button fd-button--transparent fd-button--compact'>
                Cancel
              </button>
            </div> */}
          </div>
        </div>
      </footer>
    </main>
  );
};
export default BasicPage;
