import { useCallback, useEffect, useState } from "react";
import {
  BasicPage,
  CandleStickChart,
  IllustratedMessage,
  MarketDataSearchDialog,
  MessageBox,
  VerticalLayout,
} from "../components";

const BASE_URI = "http://localhost:3000/api/data/market";
const ERROR_MESSAGE_FORMAT_REGEXP = /(time=)([0-9]{10})/gim;

export const MainPage = () => {
  const searchActions = {
    label: "Search stocks",
    icon: "search",
    onClick: () => {
      if (!openQueryDialog) {
        setOpenQueryDialog(true);
      }
    },
  };
  const [openQueryDialog, setOpenQueryDialog] = useState<boolean>(false);
  const [chartData, setChartData] = useState<IChart>();
  const [errorData, setErrorData] = useState<{
    code: string;
    description: string;
  } | null>();
  const [queryParams, setQueryParams] = useState<IMarketDataQueryParams>();
  // ({
  //   symbol: "sapgf",
  //   interval: "1d",
  //   dateRange: "1y",
  // });

  const getData = useCallback(async (url: string) => {
    const { payload } = await (await fetch(url)).json();
    if (payload.error) {
      setErrorData(payload.error);
    } else {
      setChartData({
        ...payload,
        dataPoints: payload.dataPoints || [],
      });
    }
  }, []);

  useEffect(() => {
    if (queryParams?.symbol?.length) {
      const searchParams = { ...queryParams } as IMarketDataQueryParams;

      if (queryParams?.startDate) {
        searchParams.startDate = new Date(queryParams.startDate)
          .getTime()
          .toString();
      }

      if (queryParams?.endDate) {
        searchParams.endDate = new Date(queryParams.endDate)
          .getTime()
          .toString();
      }

      const params = new URLSearchParams(searchParams);
      getData(`${BASE_URI}?${params}`);
    } else {
      setChartData(undefined);
    }
  }, [getData, queryParams]);

  return (
    <BasicPage
      className="App-page"
      headerTitle="Stock Market Data Viewer"
      headerAction={searchActions}
    >
      <VerticalLayout noGap={true} items={[]} />
      {chartData && chartData.dataPoints.length ? (
        <CandleStickChart chartData={chartData} />
      ) : (
        <IllustratedMessage
          title="Nothing to see here"
          message="Please start by searching for a stock symbol"
          actions={[searchActions]}
        />
      )}
      {openQueryDialog && (
        <MarketDataSearchDialog
          queryParams={queryParams}
          onCancelActionClick={() => setOpenQueryDialog(false)}
          onSearchActionClick={(dataQueryParams) => {
            setQueryParams(dataQueryParams);
            setOpenQueryDialog(false);
          }}
        />
      )}
      {errorData && (
        <MessageBox
          title={errorData.code}
          type="Error"
          onCloseClick={() => setErrorData(null)}
        >
          {errorData.description.replace(
            ERROR_MESSAGE_FORMAT_REGEXP,
            (...args) => {
              return `${args[1]}${new Date(
                Number(args[2]) * 1000
              ).toLocaleString()}`;
            }
          )}
        </MessageBox>
      )}
    </BasicPage>
  );
};
export default MainPage;
