import { ReactNode } from "react";

let idCounter = 0;

export interface IFormGroup {
  headerText?: string;
  items?: ReactNode[];
  horizontalLayout?: boolean;
  gridColStyleClasses?: string;
}

export const FormGroup = ({
  headerText,
  items,
  horizontalLayout,
  gridColStyleClasses,
}: IFormGroup) => {
  const styleClasses = ["fd-form-group"];
  const itemStyleClasses = horizontalLayout
    ? "fd-form-item fd-form-item--horizontal"
    : "fd-form-item";
  const hdrId = `group-header-${idCounter++}`;

  if (gridColStyleClasses?.length) {
    styleClasses.push("fd-col__form-group fd-col");
    styleClasses.push(gridColStyleClasses);
  }

  return (
    <div
      className={styleClasses.join(" ")}
      role="group"
      aria-labelledby={headerText && hdrId}
    >
      {headerText && (
        <div className="fd-form-group__header fd-form-group__header--compact">
          <h1 className="fd-form-group__header-text" id={hdrId}>
            {headerText}
          </h1>
        </div>
      )}
      {items?.map((item, i) => {
        return (
          <div key={`${hdrId}__item-${i}`} className={itemStyleClasses}>
            {item}
          </div>
        );
      })}
    </div>
  );
};
export default FormGroup;
