import React, { LegacyRef, useEffect, useRef } from "react";

const { Highcharts } = window as any;

export class DataPoint implements IDataPoint {
  timestamp: number;
  close: number;
  high: number;
  low: number;
  open: number;
  volume: number;

  constructor({ timestamp, open, high, low, close, volume }: IDataPoint) {
    this.timestamp = parseInt(timestamp.toString());
    this.open = Number(open.toFixed(2));
    this.close = Number(close.toFixed(2));
    this.high = Number(high.toFixed(2));
    this.low = Number(low.toFixed(2));
    this.volume = parseInt(volume.toString());
  }

  getCandlestickData() {
    return [this.timestamp, this.open, this.high, this.low, this.close];
  }

  getVolumeData() {
    return [this.timestamp, this.volume];
  }
}

export interface ICandleStickChartProps {
  chartData: IChart;
}

/**
 * Splits the data set into candles and volumes
 */
const parseDataPoints = (dataPoints: IDataPoint[]) => {
  const candles = [];
  const volumes = [];

  for (let point: DataPoint, len = dataPoints.length, i = 0; i < len; i++) {
    point = new DataPoint(dataPoints[i]);
    candles.push(point.getCandlestickData());
    volumes.push(point.getVolumeData());
  }
  return { candles, volumes };
};

export function CandleStickChart({ chartData }: ICandleStickChartProps) {
  const {
    dataPoints,
    symbol,
    interval,
    currency,
    regularMarketTime,
    regularMarketPrice,
    chartPreviousClose,
    dataGranularity,
    range,
    exchangeTimezoneName,
  } = chartData;
  const divRef = useRef<LegacyRef<HTMLDivElement>>() as any;

  useEffect(() => {
    if (dataPoints && divRef?.current) {
      const { candles, volumes } = parseDataPoints(dataPoints);

      // Use Highcharts modules from https://code.highcharts.com/
      // Highcharts - JS API Reference:
      // - Charts: https://api.highcharts.com/highcharts/
      // - Stock Charts: https://api.highcharts.com/highstock/
      const chart = Highcharts.stockChart(divRef?.current, {
        credits: false,
        chart: {
          zoomType: "x",
        },

        title: {
          text: `<b>${symbol}</b> - Historical Data Chart`,
          align: "left",
        },

        subtitle: {
          text: `Data Granularity: <b>${dataGranularity.toLocaleUpperCase()}</b> | Interval: <b>${interval?.toLocaleUpperCase()}</b> | Range: <b>${range.toLocaleUpperCase()}</b> | Currency: <b>${currency.toLocaleUpperCase()}</b> | Previous Close: <b>${Number(
            chartPreviousClose.toFixed(2)
          )} ${currency.toLocaleUpperCase()}</b> | Regular Market Price: <b>${Number(
            regularMarketPrice.toFixed(2)
          )} ${currency.toLocaleUpperCase()}</b> | Regular Market Time: <b>${new Date(
            regularMarketTime
          ).toLocaleString()}</b>`,
          align: "left",
        },

        time: {
          timezone: exchangeTimezoneName,
        },

        yAxis: [
          {
            crosshair: true,
            labels: {
              align: "left",
            },
            height: "80%",
            resize: {
              enabled: true,
            },
          },
          {
            labels: {
              align: "left",
            },
            top: "80%",
            height: "20%",
            resize: {
              enabled: true,
            },
            offset: 0,
          },
        ],
        tooltip: {
          shape: "square",
          headerShape: "callout",
          borderWidth: 0,
          shadow: true,
          followPointer: true,
          positioner: (width: number, height: number, point: any) => {
            return point.isHeader
              ? { x: point.plotX, y: point.plotY }
              : {
                  x:
                    point.plotX < chart.plotWidth / 2
                      ? point.plotX + 80
                      : point.plotX - 70,
                  y: point.series.yAxis.top - chart.plotTop,
                };
          },
        },

        legend: {
          enabled: true,
        },

        plotOptions: {
          series: {
            showInLegend: true,
            marker: {
              radius: 0,
            },
          },
        },

        series: [
          {
            type: "hollowcandlestick",
            id: `${symbol}-ohlc`,
            name: `${symbol} Stock Price`,
            data: candles,
          },
          {
            type: "column",
            id: `${symbol}-volume`,
            name: `${symbol} Volume`,
            data: volumes,
            yAxis: 1,
          },
        ],
        responsive: {
          rules: [
            {
              condition: {
                maxWidth: 800,
              },
              chartOptions: {
                rangeSelector: {
                  inputEnabled: true,
                },
              },
            },
          ],
        },
      });
    }
  }, [
    chartPreviousClose,
    currency,
    dataGranularity,
    dataPoints,
    exchangeTimezoneName,
    interval,
    range,
    regularMarketPrice,
    regularMarketTime,
    symbol,
  ]);

  return (
    <div
      className="chart-container"
      ref={divRef}
      style={{ width: "100%", height: "100%" }}
    ></div>
  );
}
export default CandleStickChart;
