import { ComponentProps, PropsWithChildren, SyntheticEvent } from "react";

let idCounter = 0;

export interface ISwitchProps
  extends PropsWithChildren<any>,
    ComponentProps<any> {
  id?: string;
  label?: string;
  description?: string;
  name?: string;
  autoFocus?: boolean;
  isOn?: boolean;
  readOnly?: boolean;
  disabled?: boolean;
  required?: boolean;
  state?: "error" | "success" | "warning" | "information";
  onChange?(event: SyntheticEvent): void;
}

export const Switch = ({
  id,
  label,
  description,
  name,
  autoFocus,
  isOn,
  readOnly,
  disabled,
  required,
  state,
  onChange,
}: ISwitchProps) => {
  const componentId = id || `switch-${idCounter++}`;
  const styleClasses = ["fd-switch"];

  if (state) {
    styleClasses.push(`is-${state}`);
  }

  if (disabled) {
    styleClasses.push(`is-disabled`);
  }

  return (
    <>
      {label && (
        <div className="fd-form-label" id={`${componentId}_label`}>
          {label}
        </div>
      )}

      <label className={styleClasses.join(" ")}>
        <span className="fd-switch__control">
          <input
            id={componentId}
            className="fd-switch__input"
            aria-labelledby={label && `${componentId}_label`}
            type="checkbox"
            name={name}
            checked={isOn}
            autoFocus={autoFocus}
            readOnly={readOnly}
            required={required}
            onChange={onChange}
          />
          <div className="fd-switch__slider">
            <div className="fd-switch__track">
              <span className="fd-switch__handle" role="presentation"></span>
            </div>
          </div>
        </span>
      </label>

      {description && (
        <small className="fd-text font-size-small">{description}</small>
      )}
    </>
  );
};
export default Switch;
