import {
  ComponentProps,
  PropsWithChildren,
  SyntheticEvent,
  useEffect,
  useState,
} from "react";

let idCounter = 0;

export interface ISelectOption {
  label: string;
  value: string;
}

export interface ISelectProps
  extends PropsWithChildren<any>,
    ComponentProps<any> {
  id?: string;
  label?: string;
  name?: string;
  options?: ISelectOption[];
  value?: string;
  disabled?: boolean;
  required?: boolean;
  description?: string;
  onChange?(event: SyntheticEvent): void;
}

export const Select = ({
  id,
  label,
  name,
  options,
  value,
  disabled,
  required,
  description,
  onChange,
}: ISelectProps) => {
  const [val, setVal] = useState<string>(value || "");
  const [showPopup, setShowPopup] = useState<boolean>(false);
  const [selectedOption, setSelectedOption] = useState<ISelectOption>();
  const componentId = id || `input-${idCounter++}`;

  const labelField = (
    <label
      id={`select-label-${componentId}`}
      className={`fd-form-label ${required ? "fd-input__label--required" : ""}`}
      htmlFor={componentId}
    >
      {label}
    </label>
  );

  const optionItems = options?.map((option, i) => {
    const v = option.value;
    return (
      <li
        className="fd-list__item"
        key={`select-option-${componentId}-${i}`}
        role="option"
        aria-selected={v === selectedOption?.value}
        tabIndex={v === selectedOption?.value ? 0 : -1}
        data-value={v}
        data-name={name}
        data-type="select-option"
        onClick={async (event) => {
          setSelectedOption({ ...option });
          setShowPopup(false);
          setVal((event.target as HTMLSelectElement)?.value);
          onChange?.(event);
        }}
      >
        <span
          className="fd-list__title"
          data-value={v}
          data-name={name}
          data-type="select-option"
        >
          {option.label}
        </span>
      </li>
    );
  });

  useEffect(() => {
    const option = options?.find(
      (option) => (option.value || option.label) === val
    );
    if (option) {
      setSelectedOption(option);
    }
  }, [options, val]);

  return (
    <>
      {label && labelField}

      <div
        id={`select-${componentId}`}
        className="fd-popover"
        data-name={name || null}
        data-type="select-option"
      >
        <div className="fd-popover__control">
          <div
            className="fd-select"
            style={{
              position: "fixed",
              zIndex: 100,
            }}
          >
            <button
              id={`select-button-${componentId}`}
              onClick={() => setShowPopup(!showPopup)}
              className="fd-select__control"
              tabIndex={0}
              aria-labelledby={`select-label-${componentId} select-value-${componentId}`}
              aria-expanded={showPopup === true}
              aria-haspopup="listbox"
            >
              <span
                id={`select-value-${componentId}`}
                className="fd-select__text-content"
              >
                {selectedOption?.label}
              </span>
              <span className="fd-button fd-button--transparent fd-select__button">
                <i className="sap-icon--slim-arrow-down"></i>
              </span>
            </button>
            <div
              id={`select-popup-${componentId}`}
              className="fd-popover__body fd-popover__body--no-arrow fd-popover__body--dropdown"
              aria-hidden={!showPopup}
            >
              <ul
                aria-labelledby={`select-label-${componentId}`}
                className="fd-list fd-list--dropdown"
                role="listbox"
                style={{
                  maxHeight: "13rem",
                  overflowY: "auto",
                }}
              >
                {optionItems}
              </ul>
            </div>
          </div>
        </div>
      </div>

      {description && (
        <small
          className="fd-text font-size-small"
          style={{ marginTop: "2.75rem" }}
        >
          {description}
        </small>
      )}
    </>
  );
};
export default Select;
