import { SyntheticEvent } from "react";

export interface IButton {
  text?: string;
  iconLeft?: string;
  iconRight?: string;
  badge?: string;
  disabled?: boolean | "focusable";
  selected?: boolean;
  type?:
    | "emphasized"
    | "ghost"
    | "positive"
    | "negative"
    | "attention"
    | "transparent";
  onClick?(event?: SyntheticEvent): void;
}

export const Button = ({
  badge,
  iconLeft,
  iconRight,
  disabled,
  selected,
  text,
  type,
  onClick,
}: IButton) => {
  const styleClasses = ["fd-button"];
  const badgeText = typeof badge === "undefined" ? "" : `${badge}`;

  if (type) {
    styleClasses.push(`fd-button--${type}`);
  }
  if (disabled === "focusable") {
    styleClasses.push("is-disabled");
  }
  if (selected) {
    styleClasses.push("is-selected");
  }
  return (
    <button
      className={styleClasses.join(" ")}
      aria-disabled={disabled === "focusable"}
      disabled={disabled === true}
      onClick={onClick}
    >
      {iconLeft && <i className={`sap-icon--${iconLeft}`}></i>}
      {text && <span className="fd-button__text">{text}</span>}
      {iconRight && <i className={`sap-icon--${iconRight}`}></i>}
      {badgeText && (
        <span className="fd-button__badge">
          {badgeText.length > 4 ? badgeText.substring(0, 4) : badgeText}
        </span>
      )}
    </button>
  );
};
export default Button;
