import {
  ChangeEvent,
  ComponentProps,
  FC,
  PropsWithChildren,
  useCallback,
  useState,
} from "react";
import FormGroup from "./FormGroup";
import Input from "./Input";
import Select, { ISelectOption } from "./Select";
import Switch from "./Switch";

const MARKET_EVENT_OPTIONS = [
  {
    value: "div|split|earn",
    label: "All",
  },
  {
    value: "div",
    label: "Dividends Paid",
  },
  {
    value: "split",
    label: "Splits",
  },
  {
    value: "earn",
    label: "Earning Reports",
  },
];
const TIME_SELECT_OPTIONS = [
  {
    value: "1m",
    label: "1 minute",
  },
  {
    value: "2m",
    label: "2 minutes",
  },
  {
    value: "5m",
    label: "5 minutes",
  },
  {
    value: "15m",
    label: "15 minutes",
  },
  {
    value: "30m",
    label: "30 minutes",
  },
  {
    value: "60m",
    label: "60 minutes",
  },
  {
    value: "1d",
    label: "1 day",
  },
  {
    value: "5d",
    label: "5 days",
  },
  {
    value: "1mo",
    label: "1 month",
  },
  {
    value: "3mo",
    label: "3 months",
  },
  {
    value: "6mo",
    label: "6 months",
  },
  {
    value: "1y",
    label: "1 year",
  },
  {
    value: "2y",
    label: "2 years",
  },
  {
    value: "5y",
    label: "5 years",
  },
  {
    value: "10y",
    label: "10 years",
  },
  {
    value: "ytd",
    label: "YTD",
  },
  {
    value: "max",
    label: "MAX",
  },
] as ISelectOption[];

let idCounter = 0;

export interface IMarketDataSearchDialogProps
  extends PropsWithChildren<any>,
    ComponentProps<any> {
  queryParams?: IMarketDataQueryParams;
  onCancelActionClick?(): void;
  onSearchActionClick?(dataQueryParams: IMarketDataQueryParams): void;
}

export const MarketDataSearchDialog: FC<IMarketDataSearchDialogProps> = ({
  queryParams,
  onCancelActionClick,
  onSearchActionClick,
}: IMarketDataSearchDialogProps) => {
  const [params, setParams] = useState<IMarketDataQueryParams>(
    queryParams || ({} as IMarketDataQueryParams)
  );

  const clearAction = useCallback(() => {
    setParams({
      symbol: "",
    });
  }, []);
  const cancelAction = useCallback(
    () => onCancelActionClick?.(),
    [onCancelActionClick]
  );
  const searchAction = useCallback(
    () => onSearchActionClick?.(params),
    [onSearchActionClick, params]
  );
  const updateFieldValue = ({ target }: ChangeEvent<HTMLInputElement>) => {
    const { value, name, type } = target.dataset as Record<string, string>;
    if (type === "select-option") {
      setParams({
        ...params,
        [name]: name === "symbol" ? value.toUpperCase() : value,
      });
    } else if (target.type === "checkbox") {
      setParams({
        ...params,
        [target.name]: target.checked.toString(),
      });
    } else if (target.value) {
      setParams({
        ...params,
        [target.name]:
          target.name === "symbol" ? target.value.toUpperCase() : target.value,
      });
    }
  };

  return (
    <section
      className="fd-dialog-docs-static fd-dialog fd-dialog--active"
      onKeyUp={async ({ key, altKey, ctrlKey, shiftKey }) => {
        if (key === "Escape" && !(altKey || ctrlKey || shiftKey)) {
          await cancelAction();
        }
      }}
    >
      <div
        className="fd-dialog__content fd-dialog__content--s"
        role="dialog"
        aria-modal="true"
        aria-labelledby={`dialog-title-${idCounter}"`}
        style={{
          minWidth: "32rem",
        }}
      >
        <header className="fd-dialog__header fd-bar fd-bar--header">
          <div className="fd-bar__left">
            <div className="fd-bar__element">
              <h2
                className="fd-title fd-title--h5"
                id={`dialog-title-${idCounter}"`}
              >
                Market Data Search
              </h2>
            </div>
          </div>
        </header>
        <div className="fd-dialog__body">
          <div className="fd-form-layout-grid-container fd-form-layout-grid-container--vertical fd-form-group">
            <div className="fd-row">
              <div className="fd-col fd-col--6">
                <FormGroup
                  items={[
                    <Input
                      label="Symbol:"
                      placeholder="The stock symbol to search for"
                      description="The stock symbol to search for"
                      type="text"
                      name="symbol"
                      required
                      autoFocus
                      value={params.symbol}
                      onChange={updateFieldValue}
                      onKeyUp={async ({ key, altKey, ctrlKey, shiftKey }) => {
                        if (
                          key === "Enter" &&
                          !(altKey || ctrlKey || shiftKey)
                        ) {
                          await searchAction();
                        }
                      }}
                    />,
                  ]}
                />
              </div>

              <div className="fd-col fd-col--3">
                <FormGroup
                  items={[
                    <Select
                      label="Interval"
                      name="interval"
                      value={params.interval}
                      description="The time interval for the desired market data."
                      onChange={updateFieldValue}
                      options={TIME_SELECT_OPTIONS}
                    />,
                  ]}
                />
              </div>

              <div className="fd-col fd-col--3">
                <FormGroup
                  items={[
                    <Select
                      label="Date Range"
                      name="dateRange"
                      description="The date range. It must not be with 'Start Date' and 'End Date'."
                      value={params.dateRange}
                      onChange={updateFieldValue}
                      options={TIME_SELECT_OPTIONS}
                    />,
                  ]}
                />
              </div>
            </div>

            <div className="fd-row sap-margin-top-medium">
              <div className="fd-col fd-col--6">
                <FormGroup
                  items={[
                    <Input
                      label="Start Date:"
                      placeholder="The start date"
                      description="The start date. It must be different from 'End Date' and not be used with 'Date Range'."
                      type="datetime"
                      name="startDate"
                      value={params.startDate}
                      onChange={updateFieldValue}
                      onKeyUp={async ({ key, altKey, ctrlKey, shiftKey }) => {
                        if (
                          key === "Enter" &&
                          !(altKey || ctrlKey || shiftKey)
                        ) {
                          await searchAction();
                        }
                      }}
                    />,
                  ]}
                />
              </div>

              <div className="fd-col fd-col--6">
                <FormGroup
                  items={[
                    <Input
                      label="End Date:"
                      placeholder="The end date"
                      description="The end date. It must be different from 'Start Date' and not be used with 'Date Range'."
                      type="datetime"
                      name="endDate"
                      value={params.endDate}
                      onChange={updateFieldValue}
                      onKeyUp={async ({ key, altKey, ctrlKey, shiftKey }) => {
                        if (
                          key === "Enter" &&
                          !(altKey || ctrlKey || shiftKey)
                        ) {
                          await searchAction();
                        }
                      }}
                    />,
                  ]}
                />
              </div>
            </div>

            <div className="fd-row sap-margin-top-medium">
              <div className="fd-col fd-col--6">
                <FormGroup
                  items={[
                    <Select
                      label="Market Events"
                      name="events"
                      value={params.events}
                      description="Include events like 'dividends paid', 'splits' or 'earning reports'."
                      onChange={updateFieldValue}
                      options={MARKET_EVENT_OPTIONS}
                    />,
                  ]}
                />
              </div>

              <div className="fd-col fd-col--6">
                <FormGroup
                  items={[
                    <Switch
                      label="Include Pre and Post market data"
                      name="includePrePost"
                      description="Include pre and post market data'."
                      isOn={params.includePrePost === "true" ? true : false}
                      onChange={updateFieldValue}
                    />,
                  ]}
                />
              </div>
            </div>
          </div>
        </div>

        <footer className="fd-dialog__footer fd-bar fd-bar--footer">
          <div className="fd-bar__right">
            <div className="fd-bar__element">
              <button
                className="fd-dialog__decisive-button fd-button fd-button--transparent"
                onClick={clearAction}
              >
                Clear
              </button>
            </div>
          </div>
          <div className="fd-bar__right">
            <div className="fd-bar__element">
              <button
                className="fd-dialog__decisive-button fd-button fd-button--emphasized"
                onClick={searchAction}
              >
                Search
              </button>
            </div>
            <div className="fd-bar__element">
              <button
                className="fd-dialog__decisive-button fd-button fd-button--transparent"
                onClick={cancelAction}
              >
                Cancel
              </button>
            </div>
          </div>
        </footer>
      </div>
    </section>
  );
};
export default MarketDataSearchDialog;
