import { ComponentProps, FC, PropsWithChildren, ReactNode } from "react";

export interface IVerticalLayout
  extends PropsWithChildren<any>,
    ComponentProps<any> {
  noGap?: boolean;
  items?: ReactNode[];
}

/**
 * The vertical layout grid separates the items into rows.
 *
 * @param {IVerticalLayout} params
 * @returns FC<IVerticalLayout>
 */
export const VerticalLayout: FC<IVerticalLayout> = ({
  noGap,
  items,
  className,
}: IVerticalLayout) => {
  return (
    <div
      className={`fd-container ${noGap && "fd-container--no-vertical-gap"} ${
        className || ""
      }`}
    >
      {items?.map((item, i) => {
        return (
          <div key={i} className="fd-row">
            <div className={`fd-col fd-col--12`}>{item}</div>
          </div>
        );
      })}
    </div>
  );
};
export default VerticalLayout;
