import {
  ComponentProps,
  KeyboardEventHandler,
  PropsWithChildren,
  SyntheticEvent,
  useState,
} from "react";

let idCounter = 0;

export interface IInputProps
  extends PropsWithChildren<any>,
    ComponentProps<any> {
  id?: string;
  label?: string;
  description?: string;
  name?: string;
  autoFocus?: boolean;
  multipleLines?: boolean;
  maxLength?: number;
  type?:
    | "color"
    | "date"
    | "datetime"
    | "email"
    | "hidden"
    | "number"
    | "password"
    | "range"
    | "reset"
    | "tel"
    | "text"
    | "time"
    | "url";
  placeholder?: string;
  popupMessage?: string;
  value?: string;
  readOnly?: boolean;
  disabled?: boolean;
  required?: boolean;
  hasPopup?: boolean;
  state?: "error" | "success" | "warning" | "information";
  onChange?(event: SyntheticEvent): void;
  onKeyDown?: KeyboardEventHandler<HTMLInputElement | HTMLTextAreaElement>;
  onKeyUp?: KeyboardEventHandler<HTMLInputElement | HTMLTextAreaElement>;
}

export const Input = ({
  id,
  label,
  description,
  name,
  autoFocus,
  multipleLines,
  maxLength,
  value,
  type = "text",
  placeholder,
  popupMessage,
  readOnly,
  disabled,
  required,
  state,
  hasPopup,
  onChange,
  onKeyDown,
  onKeyUp,
}: IInputProps) => {
  const [val, setVal] = useState<string>(value || "");
  const [showPopup, setShowPopup] = useState<boolean>(false);
  const styleClasses = multipleLines ? ["fd-textarea"] : ["fd-input"];
  const lblId = id || `input-${idCounter++}`;

  const onClickHandler = () => {
    setShowPopup(!showPopup);
  };
  const onChangeHandler = (event: SyntheticEvent) => {
    setVal((event.target as any)?.value);
    onChange?.(event);
  };

  if (state) {
    styleClasses.push(`is-${state}`);
  }

  const labelField = label ? (
    <label
      className={`fd-form-label ${required ? "fd-input__label--required" : ""}`}
      aria-required={required === true}
      htmlFor={lblId}
    >
      {label}
    </label>
  ) : undefined;
  const inputField = multipleLines ? (
    <>
      <textarea
        className={styleClasses.join(" ")}
        id={lblId}
        name={name}
        autoFocus={autoFocus}
        readOnly={readOnly}
        disabled={disabled}
        placeholder={placeholder}
        aria-label={label}
        value={val}
        onChange={onChangeHandler}
        onKeyDown={onKeyDown}
        onKeyUp={onKeyUp}
      />
      {maxLength && (
        <div className="fd-textarea-counter">
          {maxLength - val.length} characters left
        </div>
      )}
    </>
  ) : (
    <input
      className={styleClasses.join(" ")}
      type={type === "datetime" ? "datetime-local" : type || "text"}
      id={lblId}
      name={name}
      autoFocus={autoFocus}
      readOnly={readOnly}
      disabled={disabled}
      placeholder={placeholder}
      aria-label={label}
      value={val}
      onChange={onChangeHandler}
      onKeyDown={onKeyDown}
      onKeyUp={onKeyUp}
    />
  );

  return (
    <>
      {labelField}
      {hasPopup ? (
        <div
          className="fd-popover fd-popover--input-message-group"
          onClick={onClickHandler}
        >
          <div
            className="fd-popover__control"
            aria-controls={`popover-input-${lblId}`}
            aria-expanded={showPopup === true}
            aria-haspopup="true"
          >
            {inputField}
          </div>
          {showPopup === true && (
            <div
              id={`popover-input-${lblId}`}
              className="fd-popover__body fd-popover__body--no-arrow"
              aria-hidden="false"
            >
              <div className={`fd-form-message fd-form-message--${state}`}>
                {popupMessage}
              </div>
            </div>
          )}
        </div>
      ) : (
        inputField
      )}

      {description && (
        <small className="fd-text font-size-small">{description}</small>
      )}
    </>
  );
};
export default Input;
